import styled from "@emotion/styled";
import { DivElement } from "./index.types";
import { normalizeCSSValue } from "#Utils/normalizeCSSValue";


export const Wrapper = styled.div<DivElement.Props>`
  // AnimationProps
  animation                 : ${({ animation })               => normalizeCSSValue('animation', animation)};
  animation-delay           : ${({ animationDelay })          => normalizeCSSValue('animationDelay', animationDelay)};
  animation-name            : ${({ animationName })           => normalizeCSSValue('animationName', animationName)};
  animation-duration        : ${({ animationDuration })       => normalizeCSSValue('animationDuration', animationDuration)};
  animation-fill-mode       : ${({ animationFillMode })       => normalizeCSSValue('animationFillMode', animationFillMode)};
  animation-iteration-count : ${({ animationIterationCount }) => normalizeCSSValue('animationIterationCount', animationIterationCount)};
  animation-direction       : ${({ animationDirection })      => normalizeCSSValue('animationDirection', animationDirection)};
  animation-play-state      : ${({ animationPlayState })      => normalizeCSSValue('animationPlayState', animationPlayState)};
  animation-timing-function : ${({ animationTimingFunction }) => normalizeCSSValue('animationTimingFunction', animationTimingFunction)};
  // BorderProps
  border                     : ${({ border })                 => normalizeCSSValue('border', border)};
  border-top                 : ${({ borderTop })              => normalizeCSSValue('borderTop', borderTop)};
  border-right               : ${({ borderRight })            => normalizeCSSValue('borderRight', borderRight)};
  border-bottom              : ${({ borderBottom })           => normalizeCSSValue('borderBottom', borderBottom)};
  border-left                : ${({ borderLeft })             => normalizeCSSValue('borderLeft', borderLeft)};
  border-color               : ${({ borderColor })            => normalizeCSSValue('borderColor', borderColor)};
  border-top-color           : ${({ borderTopColor })         => normalizeCSSValue('borderTopColor', borderTopColor)};
  border-right-color         : ${({ borderRightColor })       => normalizeCSSValue('borderRightColor', borderRightColor)};
  border-bottom-color        : ${({ borderBottomColor })      => normalizeCSSValue('borderBottomColor', borderBottomColor)};
  border-left-color          : ${({ borderLeftColor })        => normalizeCSSValue('borderLeftColor', borderLeftColor)};
  border-style               : ${({ borderStyle })            => normalizeCSSValue('borderStyle', borderStyle)};
  border-top-style           : ${({ borderTopStyle })         => normalizeCSSValue('borderTopStyle', borderTopStyle)};
  border-right-style         : ${({ borderRightStyle })       => normalizeCSSValue('borderRightStyle', borderRightStyle)};
  border-bottom-style        : ${({ borderBottomStyle })      => normalizeCSSValue('borderBottomStyle', borderBottomStyle)};
  border-left-style          : ${({ borderLeftStyle })        => normalizeCSSValue('borderLeftStyle', borderLeftStyle)};
  border-radius              : ${({ borderRadius })           => normalizeCSSValue('borderRadius', borderRadius)};
  border-top-right-radius    : ${({ borderTopRightRadius })   => normalizeCSSValue('borderTopRightRadius', borderTopRightRadius)};
  border-top-left-radius     : ${({ borderTopLeftRadius })    => normalizeCSSValue('borderTopLeftRadius', borderTopLeftRadius)};
  border-bottom-right-radius : ${({ borderBottomRightRadius })=> normalizeCSSValue('borderBottomRightRadius', borderBottomRightRadius)};
  border-bottom-left-radius  : ${({ borderBottomLeftRadius }) => normalizeCSSValue('borderBottomLeftRadius', borderBottomLeftRadius)};
  border-width               : ${({ borderWidth })            => normalizeCSSValue('borderWidth', borderWidth)};
  border-top-width           : ${({ borderTopWidth })         => normalizeCSSValue('borderTopWidth', borderTopWidth)};
  border-right-width         : ${({ borderRightWidth })       => normalizeCSSValue('borderRightWidth', borderRightWidth)};
  border-bottom-width        : ${({ borderBottomWidth })      => normalizeCSSValue('borderBottomWidth', borderBottomWidth)};
  border-left-width          : ${({ borderLeftWidth })        => normalizeCSSValue('borderLeftWidth', borderLeftWidth)};
  /* ColorProps */
  color            : ${({ color })                                       => normalizeCSSValue('color', color)};
  background       : ${({ background })                                  => normalizeCSSValue('background', background)};
  background-color : ${({ b, bg, bgColor, backgroundColor }) => normalizeCSSValue('b', b) ?? normalizeCSSValue('bg', bg) ?? normalizeCSSValue('bgColor', bgColor) ?? normalizeCSSValue('backgroundColor', backgroundColor)};
  box-shadow       : ${({ boxShadow })                                   => normalizeCSSValue('boxShadow', boxShadow)};



  /* DimensionProps */
  width           : ${({ w, width })        => normalizeCSSValue('w', w) ?? normalizeCSSValue('width', width)};
  height          : ${({ h, height })       => normalizeCSSValue('h', h) ?? normalizeCSSValue('height', height)};
  max-width       : ${({ maxWidth, maxW })  => normalizeCSSValue('maxWidth', maxWidth) ?? normalizeCSSValue('maxW', maxW)};
  min-width       : ${({ minWidth, minW })  => normalizeCSSValue('minWidth', minWidth) ?? normalizeCSSValue('minW', minW)};
  max-height      : ${({ maxHeight, maxH }) => normalizeCSSValue('maxHeight', maxHeight) ?? normalizeCSSValue('maxH', maxH)};
  min-height      : ${({ minHeight, minH }) => normalizeCSSValue('minHeight', minHeight) ?? normalizeCSSValue('minH', minH)};
  object-fit      : ${({ objectFit })       => normalizeCSSValue('objectFit', objectFit)};
  object-position : ${({ objectPosition })  => normalizeCSSValue('objectPosition', objectPosition)};






  /* DisplayProps */
  display    : ${({ display })    => normalizeCSSValue('display', display)};
  opacity    : ${({ opacity })    => normalizeCSSValue('opacity', opacity)};
  visibility : ${({ visibility }) => normalizeCSSValue('visibility', visibility)};
  /* DisplayProps - FlexProps */
  flex-direction  : ${({ flexDirection })  => normalizeCSSValue('flexDirection', flexDirection)};
  align-items     : ${({ alignItems })     => normalizeCSSValue('alignItems', alignItems)};
  align-content   : ${({ alignContent })   => normalizeCSSValue('alignContent', alignContent)};
  align-self      : ${({ alignSelf })      => normalizeCSSValue('alignSelf', alignSelf)};
  justify-content : ${({ justifyContent }) => normalizeCSSValue('justifyContent', justifyContent)};
  flex-wrap       : ${({ flexWrap })       => normalizeCSSValue('flexWrap', flexWrap)};
  flex-grow       : ${({ flexGrow })       => normalizeCSSValue('flexGrow', flexGrow)};
  flex-shrink     : ${({ flexShrink })     => normalizeCSSValue('flexShrink', flexShrink)};
  flex-basis      : ${({ flexBasis })      => normalizeCSSValue('flexBasis', flexBasis)};
  /* DisplayProps - GridProps */
  grid                  : ${({ grid })                => normalizeCSSValue('grid', grid)};
  grid-template         : ${({ gridTemplate })        => normalizeCSSValue('gridTemplate', gridTemplate)};
  grid-template-columns : ${({ gridTemplateColumns }) => normalizeCSSValue('gridTemplateColumns', gridTemplateColumns)};
  grid-template-rows    : ${({ gridTemplateRows })    => normalizeCSSValue('gridTemplateRows', gridTemplateRows)};
  grid-template-areas   : ${({ gridTemplateAreas })   => normalizeCSSValue('gridTemplateAreas', gridTemplateAreas)};
  grid-area             : ${({ gridArea })            => normalizeCSSValue('gridArea', gridArea)};
  grid-column           : ${({ gridColumn })          => normalizeCSSValue('gridColumn', gridColumn)};
  grid-column-start     : ${({ gridColumnStart })     => normalizeCSSValue('gridColumnStart', gridColumnStart)};
  grid-column-end       : ${({ gridColumnEnd })       => normalizeCSSValue('gridColumnEnd', gridColumnEnd)};
  grid-row              : ${({ gridRow })             => normalizeCSSValue('gridRow', gridRow)};
  grid-row-start        : ${({ gridRowStart })        => normalizeCSSValue('gridRowStart', gridRowStart)};
  grid-row-end          : ${({ gridRowEnd })          => normalizeCSSValue('gridRowEnd', gridRowEnd)};
  grid-auto-columns     : ${({ gridAutoColumns })     => normalizeCSSValue('gridAutoColumns', gridAutoColumns)};
  grid-auto-rows        : ${({ gridAutoRows })        => normalizeCSSValue('gridAutoRows', gridAutoRows)};
  grid-auto-flow        : ${({ gridAutoFlow })        => normalizeCSSValue('gridAutoFlow', gridAutoFlow)};
  grid-gap              : ${({ gridGap })             => normalizeCSSValue('gridGap', gridGap)};
  grid-row-gap          : ${({ gridRowGap })          => normalizeCSSValue('gridRowGap', gridRowGap)};
  grid-column-gap       : ${({ gridColumnGap })       => normalizeCSSValue('gridColumnGap', gridColumnGap)};
  /* DisplayProps - OverflowProps */
  overflow   : ${({ overflow })  => normalizeCSSValue('overflow', overflow)};
  overflow-x : ${({ overflowX }) => normalizeCSSValue('overflowX', overflowX)};
  overflow-y : ${({ overflowY }) => normalizeCSSValue('overflowY', overflowY)};
  // FilterProps
  filter: ${({ filter }) => normalizeCSSValue('filter', filter)};
  // FontProps
  font-family     : ${({ fontFamily }) => normalizeCSSValue('fontFamily', fontFamily)};
  font-size       : ${({ fontSize }) => normalizeCSSValue('fontSize', fontSize)};
  font-weight     : ${({ fontWeight }) => normalizeCSSValue('fontWeight', fontWeight)};
  font-style      : ${({ fontStyle }) => normalizeCSSValue('fontStyle', fontStyle)};
  text-decoration : ${({ textDecoration }) => normalizeCSSValue('textDecoration', textDecoration)};
  text-align      : ${({ textAlign }) => normalizeCSSValue('textAlign', textAlign)};
  line-height     : ${({ lineHeight }) => normalizeCSSValue('lineHeight', lineHeight)};
  text-shadow     : ${({ textShadow }) => normalizeCSSValue('textShadow', textShadow)};
  letter-spacing  : ${({ letterSpacing }) => normalizeCSSValue('letterSpacing', letterSpacing)};
  text-transform  : ${({ textTransform }) => normalizeCSSValue('textTransform', textTransform)};
  // InteractionProps
  cursor         : ${({ cursor }) => normalizeCSSValue('cursor', cursor)};
  user-select    : ${({ userSelect }) => normalizeCSSValue('userSelect', userSelect)};
  pointer-events : ${({ pointerEvents }) => normalizeCSSValue('pointerEvents', pointerEvents)};
  // MarginProps
  margin        : ${({ m, margin })            => normalizeCSSValue('m', m) ?? normalizeCSSValue('margin', margin)};
  margin-top    : ${({ mt, marginTop, my })    => normalizeCSSValue('mt', mt) ?? normalizeCSSValue('marginTop', marginTop) ?? normalizeCSSValue('my', my)};
  margin-right  : ${({ mr, marginRight, mx })  => normalizeCSSValue('mr', mr) ?? normalizeCSSValue('marginRight', marginRight) ?? normalizeCSSValue('mx', mx)};
  margin-bottom : ${({ mb, marginBottom, my }) => normalizeCSSValue('mb', mb) ?? normalizeCSSValue('marginBottom', marginBottom) ?? normalizeCSSValue('my', my)};
  margin-left   : ${({ ml, marginLeft, mx })   => normalizeCSSValue('ml', ml) ?? normalizeCSSValue('marginLeft', marginLeft) ?? normalizeCSSValue('mx', mx)};
  // PaddingProps
  padding        : ${({ p, padding })             => normalizeCSSValue('p', p) ?? normalizeCSSValue('padding', padding)};
  padding-top    : ${({ pt, paddingTop, py })     => normalizeCSSValue('pt', pt) ?? normalizeCSSValue('paddingTop', paddingTop) ?? normalizeCSSValue('py', py)};
  padding-right  : ${({ pr, paddingRight, px })   => normalizeCSSValue('pr', pr) ?? normalizeCSSValue('paddingRight', paddingRight) ?? normalizeCSSValue('px', px)};
  padding-bottom : ${({ pb, paddingBottom, py })  => normalizeCSSValue('pb', pb) ?? normalizeCSSValue('paddingBottom', paddingBottom) ?? normalizeCSSValue('py', py)};
  padding-left   : ${({ pl, paddingLeft, px })    => normalizeCSSValue('pl', pl) ?? normalizeCSSValue('paddingLeft', paddingLeft) ?? normalizeCSSValue('px', px)};
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  // PositionProps
  position  : ${({ position, pos }) => normalizeCSSValue('position', position) ?? normalizeCSSValue('pos', pos)};
  top       : ${({ top })           => normalizeCSSValue('top', top)};
  right     : ${({ right })         => normalizeCSSValue('right', right)};
  bottom    : ${({ bottom })        => normalizeCSSValue('bottom', bottom)};
  left      : ${({ left })          => normalizeCSSValue('left', left)};
  z-index   : ${({ zIndex })        => normalizeCSSValue('zIndex', zIndex)};
  clip      : ${({ clip })          => normalizeCSSValue('clip', clip)};
  clip-path : ${({ clipPath })      => normalizeCSSValue('clipPath', clipPath)};
  // TranslateProps
  transform: ${({ translate, translateX, translateY, translateZ, rotate, rotateX, rotateY, rotateZ, scale, scaleX, scaleY, scaleZ, skew, skewX, skewY, skewZ }) => {
    const transforms = [
      translate  && `translate(${normalizeCSSValue('translate', translate)})`,
      translateX && `translateX(${normalizeCSSValue('translateX', translateX)})`,
      translateY && `translateY(${normalizeCSSValue('translateY', translateY)})`,
      translateZ && `translateZ(${normalizeCSSValue('translateZ', translateZ)})`,
      rotate     && `rotate(${normalizeCSSValue('rotate', rotate)})`,
      rotateX    && `rotateX(${normalizeCSSValue('rotateX', rotateX)})`,
      rotateY    && `rotateY(${normalizeCSSValue('rotateY', rotateY)})`,
      rotateZ    && `rotateZ(${normalizeCSSValue('rotateZ', rotateZ)})`,
      scale      && `scale(${normalizeCSSValue('scale', scale)})`,
      scaleX     && `scaleX(${normalizeCSSValue('scaleX', scaleX)})`,
      scaleY     && `scaleY(${normalizeCSSValue('scaleY', scaleY)})`,
      scaleZ     && `scaleZ(${normalizeCSSValue('scaleZ', scaleZ)})`,
      skew       && `skew(${normalizeCSSValue('skew', skew)})`,
      skewX      && `skewX(${normalizeCSSValue('skewX', skewX)})`,
      skewY      && `skewY(${normalizeCSSValue('skewY', skewY)})`,
      skewZ      && `skewZ(${normalizeCSSValue('skewZ', skewZ)})`
    ].filter(Boolean).join(' ');
    return transforms || 'none';
  }};
  transform-origin           : ${({ transformOrigin }) => normalizeCSSValue('transformOrigin', transformOrigin)};
  transform-style            : ${({ transformStyle }) => normalizeCSSValue('transformStyle', transformStyle)};
  transition                 : ${({ transition }) => normalizeCSSValue('transition', transition)};
  transition-delay           : ${({ transitionDelay }) => normalizeCSSValue('transitionDelay', transitionDelay)};
  transition-duration        : ${({ transitionDuration }) => normalizeCSSValue('transitionDuration', transitionDuration)};
  transition-property        : ${({ transitionProperty }) => normalizeCSSValue('transitionProperty', transitionProperty)};
  transition-timing-function : ${({ transitionTimingFunction }) => normalizeCSSValue('transitionTimingFunction', transitionTimingFunction)};
  perspective                : ${({ perspective }) => normalizeCSSValue('perspective', perspective)};
  perspective-origin         : ${({ perspectiveOrigin }) => normalizeCSSValue('perspectiveOrigin', perspectiveOrigin)};
  backface-visibility        : ${({ backfaceVisibility }) => normalizeCSSValue('backfaceVisibility', backfaceVisibility)};
`